@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #E3B115;
}


.home_page {
	position: relative;
	height: 100vh;
	overflow-x: hidden;
}

#background_logo {
	position: absolute;
	top: 50vh;
	left: 50vw;
	transform: translate(-50%, -50%);
	z-index: -5;
	height: 80vh;
}

.main_photo {
	position: absolute;
	bottom: 0;
	right: -100vw;
	height: 100vh;
	z-index: -1;
	transition: right 1.5s ease-out;
}

.main_photo.show {
	right: 0;
}

#name {
	font-family: 'Montserrat', sans-serif;
	font-weight: bold;
	color: white;
	position: absolute;
	font-size: 96px;
	letter-spacing: .25em;
	top: calc(20vh);
	z-index: 10;
	transition: left 1.5s ease-out;
}

#lastname {
	font-family: 'Montserrat', sans-serif;
	font-weight: 300;
	color: white;
	position: absolute;
	font-size: 96px;
	letter-spacing: .1em;
	top: calc(20vh + 120px);
	z-index: 10;
	transition: left 2s ease-out;
}

#mini_description {
	font-family: 'Montserrat', sans-serif;
	font-weight: 300;
	color: white;
	position: absolute;
	font-size: 48px;
	top: calc(20vh + 300px);
	z-index: 10;
	transition: left 2.5s ease-out;
}

.hide_desc {
	left: -1000px;
}

.show_desc {
	left: 215px;
}

@media screen and (max-width: 858px) {
	.main_photo { 
		display: none;
	}

	#name {
		transform: translate(-50%, -50%);
		font-size: 15vw;
		top: calc(35vh - 20vw);
	}

	#lastname {
		transform: translate(-50%, -50%);
		font-size: 15vw;
		top: calc(35vh);
	}

	#mini_description {
		transform: translate(-50%, -50%);
		font-size: 5vw;
		top: calc(35vh + 30vw);
		width: 100vw;
		text-align: center;
	}

	.hide_desc {
		left: -100vw;
	}

	.show_desc {
		left: 50vw;
	}
}

nav {
	height: 120px;
	width: 100%;
}

.main_logo {
	padding: 25px 75px;
}

nav ul {
	float: right;
	margin: 0;
	margin-right: 20px;  
	padding: 0;
}

nav ul li {
	display: inline-block;
	line-height: 40px;
	margin: 0 20px;
}

nav ul li h3 {
	color: white;
	font-family: 'Montserrat', sans-serif;
	font-size: 18px;
	padding: 10px 10px;
	font-weight: 400;
	cursor: pointer;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.check_button {
	float: right;
	cursor: pointer;
	padding: 50px;
	display: none;
}

#check {
	display: none;
}

@media screen and (max-width: 982px) {
	.main_logo {
		padding: 25px 50px;
	}

	nav ul li h3 {
		color: white;
		font-family: 'Montserrat', sans-serif;
		font-size: 18px;
		padding: 10px 0;
		font-weight: 400;
	}
}

@media screen and (max-width: 858px) {
	nav {
		background: #E3B115;
		box-shadow: 0px 5px 5px 0px rgba(0,0,0,0.2);
		height: 80px;
	}

	.main_logo {
		padding: 5px 25px;
		max-width: 200px;
		width: auto;
		height: auto;
	}

	.check_button {
		display: block;
		padding: 25px;
	}

	ul {
		position: fixed;
		width: 100%;
		height: calc(100vh - 100px);
		background: #E3B115;
		z-index: 15;
		top: 80px;
		left: -100%;
		text-align: center;
		transition: all .5s;
	}

	nav ul li {
		display: block;
		margin: 50px;
	}

	nav ul li h3 {
		font-size: 24px;
		font-weight: 600;
		text-transform: uppercase;
	}

	h3:hover {
		color: #FE6;
	}

	#check:checked ~ ul {
		left: 0;
	}
}

.social_media {
	position: absolute;
	margin: 20px 50px;
	bottom: 0;
	z-index: 10;
}

.social_media > img {
	padding: 10px;
}

@media screen and (max-width: 768px) {
	.social_media {
		position: relative;
		margin: 0;
		width: 100vw;
		top: calc(100vh - 175px);
		display: flex;
		justify-content: space-evenly;
		align-items: center;
	}

	.social_media > img {
		padding: 0;
	}
}

